import Vue from 'vue';
import loadingBar from './loading-bar.vue';

let loadingBarInstance;
let timer;
/**
 * loadingBar
 */
const newInstance = (properties = {}) => {
  loadingBarInstance =
    loadingBarInstance ||
    new Vue({
      data: properties,
      render(h) {
        return h(loadingBar);
      },
    });

  const compile = loadingBarInstance.$mount();
  document.body.appendChild(compile.$el);
  return loadingBarInstance;
};

/**
 * 
 */
const getCompont = () => (loadingBarInstance || newInstance()).$children[0];

/**
 * d
 */
const clearTimer = () => {
  clearInterval(timer);
};

/**
 * 
 */
const reset = () => {
  const compont = getCompont();
  compont.status = 'primary';
  compont.percent = 0;
};

export default {
  //  0 
  start() {
    // ,,
    setTimeout(() => {
      const compont = getCompont();
      if (compont.percent > 0) {
        return;
      }
      let percent = 0;
      timer = setInterval(() => {
        percent += Math.floor(Math.random() * 3 + 1);
        if (percent > 90) {
          clearTimer();
        }
        compont.percent = percent;
      }, 200);
    }, 100);
  },
  // 
  finish() {
    const compont = getCompont();
    // loading
    clearTimer();
    compont.percent = 100;
    setTimeout(() => {
      reset();
    }, 800);
  },
  // 
  error() {
    const compont = getCompont();
    clearInterval(timer);
    compont.percent = 100;
    compont.status = 'error';
    setTimeout(() => {
      reset();
    }, 800);
  },
  /**
   * 
   * @param percent 
   */
  update(percent) {
    const compont = getCompont();
    clearTimer();
    compont.percent = percent;
  },
  destory() {
    loadingBarInstance = null;
    clearTimer();
    document.body.removeChild(document.querySelector('.py-loading-bar'));
  },
};
