<template>
  <transition name="py-notification-fade">
    <div :class="['py-notification', customClass, horizontalClass]"
    v-show="visible" :style="positionStyle"
    @click="click" @mouseenter="clearTimer()" @mouseleave="startTimer()">
      <i class="py-notification__icon pyui-icons"
      :class="[ typeClass, iconClass ]"
      v-if="type || iconClass"></i>
      <div class="py-notification__group">
        <h2 class="py-notification__title" v-text="title"></h2>
        <div class="py-notification__content" v-show="message">
          <slot>
            <p v-if="!dangerouslyUseHTMLString">{{ message }}</p>
            <p v-else v-html="message"></p>
          </slot>
        </div>
        <i class="py-notification__closeBtn pyui-icons py-icon-close"
        v-if="showClose"
        @click.stop="close"></i>
      </div>
    </div>
  </transition>
</template>

<script>
// import PyIcon from '../../icon/index.js';
/**
 * type
 */
const typeMap = {
  success: 'check-circle-fill',
  info: 'info-circle-fill',
  warning: 'warning-circle-fill',
  error: 'close-circle-fill',
};
export default {
  name: 'Notification',
  data() {
    return {
      // 
      visible: false,
      // 
      title: '',
      // 
      message: '',
      // ,  0 
      duration: 4500,
      // this.$notify.info
      type: '',
      // 
      showClose: true,
      // 
      customClass: '',
      //  type iconClass 
      iconClass: '',
      // 
      onClose: null,
      //  Notification 
      onClick: null,
      // 
      closed: false,
      // offset
      verticalOffset: 0,
      // 
      timer: null,
      //  message  HTML true
      dangerouslyUseHTMLString: false,
      // top-right/top-left/bottom-right/bottom-left
      position: 'top-right',
    };
  },
  // components: {
  //   'py-icon': PyIcon,
  // },
  watch: {
    closed(newVal) {
      if (newVal) {
        this.visible = false;
        this.$el.addEventListener('transitionend', this.destroyElement);
      }
    },
  },
  computed: {
    // 
    typeClass() {
      return this.type && typeMap[this.type] ? `py-icon-${typeMap[this.type]}` : '';
    },
    // 
    horizontalClass() {
      return this.position.indexOf('right') > -1 ? 'right' : 'left';
    },
    // 
    verticalProperty() {
      return /^top-/.test(this.position) ? 'top' : 'bottom';
    },
    positionStyle() {
      return {
        [this.verticalProperty]: `${this.verticalOffset}px`,
      };
    },
  },
  methods: {
    destroyElement() {
      this.$el.removeEventListener('transitionend', this.destroyElement);
      this.$destroy(true);
      this.$el.parentNode.removeChild(this.$el);
    },
    click() {
      if (typeof this.onClick === 'function') {
        this.onClick();
      }
    },
    close() {
      this.closed = true;
      if (typeof this.onClose === 'function') {
        this.onClose();
      }
    },
    clearTimer() {
      clearTimeout(this.timer);
    },
    startTimer() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          if (!this.closed) {
            this.close();
          }
        }, this.duration);
      }
    },
    keydown(e) {
      if (e.keyCode === 46 || e.keyCode === 8) {
        // Detele  BackSpace  
        this.clearTimer();
      } else if (e.keyCode === 27) {
        // esc
        this.clearTimer(); // detele 
      } else if (e.keyCode === 27) {
        // esc
        if (!this.closed) {
          this.close();
        }
      } else {
        // 
        this.startTimer();
        this.startTimer(); // 
      }
    },
  },
  mounted() {
    if (this.duration > 0) {
      // 
      this.timer = setTimeout(() => {
        if (!this.closed) {
          this.close();
        }
      }, this.duration);
    }
    document.addEventListener('keydown', this.keydown);
  },
  beforeDestroy() {
    document.removeEventListener('keydown', this.keydown);
  },
};
</script>
