const navData = [
  {
    name: '',
    path: 'quick-start',
  },
  {
    name: '',
    path: 'changelog',
  },
  {
    name: '',
    children: [
      {
        group: 'Basic',
        children: [
          {
            name: 'Layout ',
            path: 'layout',
          },
          {
            name: 'Button ',
            path: 'button',
          },
        ],
      },
      {
        group: 'Form',
        children: [
          {
            name: 'Input ',
            path: 'input',
          },
        ],
      },
      {
        group: 'Data',
        children: [
          {
            name: 'Badge ',
            path: 'badge',
          },
          {
            name: 'loadingBar ',
            path: 'loading-bar',
          },
        ],
      },
      {
        group: 'Notice',
        children: [
          {
            name: 'Alert ',
            path: 'alert',
          },
        ],
      },
      {
        group: 'Navigation',
        children: [
          {
            name: 'Tabs ',
            path: 'tabs',
          },
        ],
      },
    ],
  },
];

const nav = navData.reduce((init, item) => {
  if (item.children) {
    init.push({
      name: item.name,
      type: 'first-level',
    });

    item.children.forEach(({ group, children }) => {
      init.push({ name: group, type: 'group', children });
    });
  } else {
    init.push({
      ...item,
      type: 'first-level',
    });
  }

  return init;
}, []);

export default nav;
